import * as d3 from 'd3';
import React, { useState } from 'react';
import { useEffect, useRef } from 'react';
import { IAPIGameDetails } from '../../util/types/APITypes';

interface IShotVisualizerProps {
    data: IAPIGameDetails
}

const ShotVisualizer = (props: IShotVisualizerProps) => {
    const ref = useRef<HTMLDivElement>(null);
    // const [svg, setSvg] = useState<d3.Selection<SVGSVGElement, unknown, null, undefined>>();

    useEffect( () => {
        props.data != null && drawRink();
    }, []);

    const drawRink = () => {
        let svg = d3.select(ref.current)
            .append('svg')
            .attr('width', 200)
            .attr('height', 85);

        // setSvg(svg);

        drawBaseRink(svg);

        // centre ice circle
        drawCircle(svg, 100, 42.5,15,'red')

        // centre ice line
        drawLine(svg, 100, 0, 100, 85, 'red');

        //goal blue ice
        drawRectangle(svg,10,38.5,4,8,'blue');
        drawRectangle(svg,186,38.5,4,8,'blue');
        
        // goal lines
        drawLine(svg, 10, 2.5, 10, 82.5, 'red');
        drawLine(svg, 190, 2.5, 190, 82.5, 'red');

        // blue lines
        drawLine(svg, 70, 0, 70, 85, 'blue');
        drawLine(svg, 130, 0, 130, 85, 'blue');

        plotGoals(svg);
    }

    const plotGoals = (svg:d3.Selection<SVGSVGElement, unknown, null, undefined>) => {
        console.log("plotting goals");
        props.data.scoringPlays.forEach( (goal) => {
            const scoringPlay = props.data.allPlays.find ( play => play.eventIDx === goal);
            if (scoringPlay != null && svg != null) {
                const colour = scoringPlay.teamID == props.data.homeStats.id ? "red" : "blue";
                const coord = rebaseCoordinate(scoringPlay.coordinates)
                svg.append("circle")
                    .attr('cx', coord.x)
                    .attr('cy', coord.y)
                    .attr('r', 1)
                    .attr('stroke', colour)
                    .attr('fill', colour);
            }
        })
    }

    const rebaseCoordinate = (coord: {x: number , y: number}): {x: number , y: number} => {
        //consider the starting rink side
        const homeSide = props.data.homeStats.startingRinkSide;
        if (homeSide == "right"){
            coord.x = - coord.x;
            coord.y = - coord.y;
        }
        return {
            x: coord.x + 100,
            y: coord.y + 42.5
        }


    }

    const drawShotMap = () => {

    }

    const drawBaseRink = (svg:d3.Selection<SVGSVGElement, unknown, null, undefined>) => {
        svg.append("rect")
            .attr('rx', 20)
            .attr('ry', 20)
            .attr("x", 0)
            .attr("y", 0)
            .attr("height", 85)
            .attr("width", 200)
            .style('opacity', 1)
            .style("fill", "white")
            .style("stroke", "white")
            .style("stroke-width", 4);
    }

    const drawLine = (svg:d3.Selection<SVGSVGElement, unknown, null, undefined>,
        x1: number, y1: number, x2: number , y2: number, colour: string) => {
        svg.append('line')
        .style("stroke", colour)
        .style("stroke-width", 1)
        .attr("x1", x1)
        .attr("y1", y1)
        .attr("x2", x2)
        .attr("y2", y2); 
    }

    const drawRectangle = (svg:d3.Selection<SVGSVGElement, unknown, null, undefined>,
        x: number, y: number, width: number , height: number, colour: string) => {
        svg.append('rect')
        .attr('x', x)
        .attr('y', y)
        .attr('width', width)
        .attr('height', height)
        .attr('stroke', colour)
        .style("stroke-width", 0.5)
        .attr('fill', '#89CFF0');
    }

    const drawCircle =  (svg:d3.Selection<SVGSVGElement, unknown, null, undefined>,
        x: number, y: number, radius: number, colour: string) => {
            svg.append('circle')
            .attr('cx', x)
            .attr('cy', y)
            .style("stroke-width", 0.5)
            .attr('r', radius)
            .attr('stroke', colour)
            .attr('fill', 'white');
        }

    return (
        <div className="shot-visualizer-div" ref={ref}/>
    )
}

export default ShotVisualizer